import { Component, OnInit, Input } from '@angular/core';
import { Post } from '../post.interface';
import { UserService } from '../../user/user.service';

@Component({
  selector: 'app-post-panel',
  templateUrl: './post-panel.component.html',
  styleUrls: ['./post-panel.component.css']
})
export class PostPanelComponent implements OnInit {
    @Input() post: Post;
    userImage: any;
    username: any;

    editValue = '';
    editing = false;

  constructor(private userService: UserService) { }

  ngOnInit() {
      this.getUsername();
      this.getActualProfileImage();
  }

  getUsername(){
      this.userService.getUser(this.post.user_id)
        .subscribe(
            (user) => {
                this.username = user.f_name + ' ' + user.l_name;
            }
        );
  }

  getActualProfileImage(){
      this.userService.getActualProfileImage(this.post.user_id)
        .subscribe(
            (image) => {
                this.userImage = image[0].filename;
            }
        );
  }

  onEdit(){
      this.editing = true;

  }

  onDelete(){

  }

  onCancel(){
      this.editing = false;
  }

  onSave(){

  }

}



